<template>
  <div class="index-bg p15">
    <HeadTop :title="t('index.SEELE DAO')"></HeadTop>
    <div class="content">
      <div class="li">
        <van-cell-group inset>
          <van-field v-model="minStake.value" type="number" :placeholder="t('index.請輸入起投數量')" />
        </van-cell-group>
        <van-button class="confim-btn" type="primary" :loading="minStake.loading" :disabled="minStake.loading" :loading-text="t('index.確定')" @click="setMinStakeHandle">{{t('index.確定')}}</van-button>
      </div>
      <div class="li">
        <van-cell-group inset>
          <van-field v-model="feeAddr.value" type="text" :placeholder="t('index.請輸入技術服務費接受地址')" />
        </van-cell-group>
        <van-button class="confim-btn" type="primary" :loading="feeAddr.loading" :disabled="feeAddr.loading" :loading-text="t('index.確定')" @click="setFeeAddrHandle">{{t('index.確定')}}</van-button>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch, getCurrentInstance, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import Footer from "@/components/Footer";
import { config } from "@/utils/config";
import stakeAbi from "@/utils/stakeAbi.json";
import BigNumber from "bignumber.js";
import {
  Toast
} from "vant";
export default {
  components: {
    HeadTop,
    Footer
  },
  setup () {
    const internalInstance = getCurrentInstance();
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      minStake: {
        loading: false,
        value: ''
      },
      feeAddr: {
        loading: false,
        value: ''
      },
      initContract: () => {
        data.stakeContract = new data.web3.eth.Contract(
          stakeAbi,
          config.pledgeAddres
        );
        data.initData()
      },
      initData: () => {
        data.getMinStakeHandle()
        data.getFeeAddrHandle()
      },
      getMinStakeHandle: async () => {
        try {
          Toast.loading({
            duration: 0,
          });
          let res = await data.stakeContract.methods.minStake().call({
            from: data.address
          });
          if (res) {
            let _minStake = BigNumber(res).dividedBy(10 ** 18)
              .toFixed();
            data.minStake.value = _minStake || ''
            Toast.clear();
          }
        } catch (error) {
          Toast.clear();
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      },
      setMinStakeHandle: async () => {
        try {
          if (!data.minStake.value) {
            return Toast({
              message: t("index.請輸入起投數量")
            });
          }
          data.minStake.loading = true
          let _minStake = new BigNumber(data.minStake.value)
            .multipliedBy(10 ** 18)
            .toFixed();
          console.log(_minStake, '_minStake')
          await data.stakeContract.methods.setMinStake(_minStake).estimateGas({
            from: data.address
          });
          await data.stakeContract.methods.setMinStake(_minStake).send({
            from: data.address
          });
          Toast({
            message: t("Success"),
            onClose: () => {
              data.minStake.loading = false
            }
          });
        } catch (error) {
          data.minStake.loading = false
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      },
      getFeeAddrHandle: async () => {
        try {
          Toast.loading({
            duration: 0,
          });
          let res = await data.stakeContract.methods.feeAddr().call({
            from: data.address
          });
          if (res) {
            data.feeAddr.value = res
            Toast.clear();
          }
        } catch (error) {
          Toast.clear();
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      },
      setFeeAddrHandle: async () => {
        try {
          let _feeAddr = data.feeAddr.value
          if (!data.web3.utils.isAddress(_feeAddr)) {
            return Toast({
              message: t("index.不是有效的以太坊地址")
            });
          }
          await data.stakeContract.methods.setFeeAddr(_feeAddr).estimateGas({
            from: data.address
          });
          await data.stakeContract.methods.setFeeAddr(_feeAddr).send({
            from: data.address
          });
          Toast({
            message: t("Success"),
            onClose: () => {
              data.feeAddr.loading = false
            }
          });
        } catch (error) {
          data.feeAddr.loading = false
          internalInstance.appContext.config.globalProperties.$errorformat(
            error
          );
        }
      }
    })
    onMounted(() => {
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
.van-cell-group {
  margin: 0 0 0;
}
.confim-btn {
  width: 125px;
  height: 40px;
  background: #409dbd;
  border-radius: 10px;
  font-size: 15px;
  font-weight: bold;
  color: #ffffff;
  margin: 12px 0 20px;
  border: 0;
}
</style>
<style lang="less" scoped>
.index-bg {
  background: #75aec1 url('~@/assets/images/index-bg.png') no-repeat;
  background-size: 100% auto;
  width: 100%;
  min-height: 100vh;
  line-height: 1.3;
}
</style>
